import { ChatOpenAI } from "@langchain/openai";
import { HumanMessage, SystemMessage } from "@langchain/core/messages";
import type { AutoGenerationRequest, AutoGenerationResponse } from "../types/quiz";

const OPENAI_API_KEY = process.env.NEXT_PUBLIC_OPENAI_API_KEY;

class QuizGenerationService {
  private model: ChatOpenAI;

  constructor() {
    if (!OPENAI_API_KEY) {
      throw new Error("OpenAI API key is required. Please set VITE_OPENAI_API_KEY in your environment variables.");
    }

    this.model = new ChatOpenAI({
      apiKey: OPENAI_API_KEY,
      model: "gpt-4o",
      temperature: 0, // „Çà„Çä‰∏ÄË≤´„Åó„ÅüÁµêÊûú„ÅÆ„Åü„ÇÅ„Å´‰Ωé„ÅèË®≠ÂÆö
    });
  }

  async generateQuiz(request: AutoGenerationRequest): Promise<AutoGenerationResponse> {
    const systemPrompt = `
    „ÅÇ„Å™„Åü„ÅØÊïôËÇ≤Áî®ÈÅ∏ÊäûÂºèÂïèÈ°å‰ΩúÊàê„ÅÆÂ∞ÇÈñÄÂÆ∂„Åß„Åô„ÄÇ‰∏é„Åà„Çâ„Çå„ÅüÂïèÈ°åÊñá„ÄÅ„Ç≥„Éº„Éâ„ÄÅÂ≠¶ÁøíÈ†ÖÁõÆ(${request.learningTopic})„Åã„Çâ„ÄÅÂ≠¶ÁøíËÄÖ„ÅÆÁêÜËß£„ÅåÊ∑±„Åæ„Çã„Çà„ÅÜ„Å™ÈÅ∏ÊäûÂºèÂïèÈ°å„Çí‰ΩúÊàê„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ

    # ÊåáÁ§∫
    1. ${request.learningTopic}„Å´Èñ¢ÈÄ£„Åô„ÇãÊúÄ„ÇÇÈáçË¶Å„Å™ÁÆáÊâÄ„Çí1„Å§ÈÅ∏„Çì„Åß„Äå__BLANK__„Äç„Å´„Åô„Çã
    2. __BLANK__„ÅØ**ÂøÖ„Åö„ÇΩ„Éº„Çπ„Ç≥„Éº„Éâ‰∏≠„Åã„ÇâÈÅ∏„Å≥**„ÄÅÊ±∫„Åó„Å¶„ÇΩ„Éº„Çπ„Ç≥„Éº„Éâ„ÇíËøΩÂä†„Åó„Å¶__BLANK__„Å´„Åó„Åü„Çä„Ç≥„É°„É≥„Éà„Ç¢„Ç¶„Éà(// „ÅÆÈÉ®ÂàÜ)„ÅÆÈÉ®ÂàÜ„Çí__BLANK__„Å´„Åó„Åü„Çä„Åó„Å™„ÅÑ
    3. __BLANK__„ÅØÊúÄÂ§ß„Åß„ÇΩ„Éº„Çπ„Ç≥„Éº„Éâ1Ë°å„Å®„Åó„ÄÅ„Å™„Çã„Åπ„ÅèÁü≠„Åè„Åô„Çã
    4. **Ê≠£Ëß£„ÅØÁµ∂ÂØæ„Å´1„Å§„ÅÆ„ÅøÔºà__BLANK__„Å®„Åó„Åü„ÇΩ„Éº„Çπ„Ç≥„Éº„Éâ„Çí„Åù„ÅÆ„Åæ„ÅæÂá∫ÂäõÔºâ**„Å®„Åó„ÄÅB„ÄÅC„ÄÅD„Å´„ÅØÊòé„Çâ„Åã„Å™Ë™§Á≠î„ÇíÂá∫Âäõ„Åô„Çã
    5. Ë™§Á≠î„ÅØË®ÄË™û„ÅÆÊñáÊ≥ï„Åß„ÅØ„Å™„Åè„ÄÅÂ≠¶ÁøíÈ†ÖÁõÆ„Å´Èñ¢ÈÄ£„Åô„Çã„ÇÇ„ÅÆ„Å®„Åó„ÄÅÂ≠¶ÁøíËÄÖ„ÅåÂ≠¶„Å≥„ÅÆ„ÅÇ„ÇãÁ¥†Êô¥„Çâ„Åó„ÅÑ„ÇÇ„ÅÆ„Å´„Åô„Çã„ÄÇ
    6. JSON„Éï„Ç©„Éº„Éû„ÉÉ„Éà„ÅßÂõûÁ≠î„Åô„Çã

    # Âá∫Âäõ„Éï„Ç©„Éº„Éû„ÉÉ„Éà
    {
      "problemText": "ÂÖÉ„ÅÆÂïèÈ°åÊñá„Çí‰øùÊåÅ„Åó„ÄÅÊú´Â∞æ„Å´„Äå___BLANK___„ÅÆÈÉ®ÂàÜ„Å´ÂΩì„Å¶„ÅØ„Åæ„ÇãÈÅ©Âàá„Å™ÈÅ∏ÊäûËÇ¢„ÇíÈÅ∏„Åπ„ÄÇ„Äç„ÇíËøΩÂä†„Åó„ÅüÂïèÈ°åÊñá",
      "codeWithBlanks": "„ÇΩ„Éº„Çπ„Ç≥„Éº„Éâ„ÅÆ‰∏ÄÈÉ®„Çí___BLANK___„Å´ÁΩÆ„ÅçÊèõ„Åà„ÄÅ„Åù„ÅÆ„Åª„Åã„ÅØÂ§âÊõ¥„Åó„Å¶„ÅÑ„Å™„ÅÑ„Ç≥„Éº„Éâ",
      "choices": [
        {"id": "A", "text": "ÈÅ∏ÊäûËÇ¢A", "isCorrect": true},
        {"id": "B", "text": "ÈÅ∏ÊäûËÇ¢B", "isCorrect": false},
        {"id": "C", "text": "ÈÅ∏ÊäûËÇ¢C", "isCorrect": false},
        {"id": "D", "text": "ÈÅ∏ÊäûËÇ¢D", "isCorrect": false}
      ],
      "correctChoiceId": "A"
    }
    `;

    const userPrompt = `
    # ÂïèÈ°åÊñá
    ${request.problemText}

    # „ÇΩ„Éº„Çπ„Ç≥„Éº„Éâ (${request.language})
    \`\`\`
    ${request.code}
    \`\`\`

    # Â≠¶ÁøíÈ†ÖÁõÆ
    ${request.learningTopic}
    `;

    try {
      const messages = [new SystemMessage(systemPrompt), new HumanMessage(userPrompt)];

      const response = await this.model.invoke(messages);
      const responseText = response.content as string;

      // JSONÈÉ®ÂàÜ„ÇíÊäΩÂá∫
      const jsonMatch = responseText.match(/\{[\s\S]*\}/);
      if (!jsonMatch) {
        throw new Error("AI„ÅÆÂøúÁ≠î„Åã„ÇâJSON„ÇíÊäΩÂá∫„Åß„Åç„Åæ„Åõ„Çì„Åß„Åó„Åü");
      }

      const parsedResponse = JSON.parse(jsonMatch[0]) as AutoGenerationResponse;

      // „Éê„É™„Éá„Éº„Ç∑„Éß„É≥
      if (!parsedResponse.codeWithBlanks.includes("___BLANK___")) {
        throw new Error("Á©∫Ê¨ÑÁÆáÊâÄ„ÅåÊ≠£„Åó„ÅèË®≠ÂÆö„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì");
      }

      if (!parsedResponse.choices || parsedResponse.choices.length !== 4) {
        throw new Error("ÈÅ∏ÊäûËÇ¢„ÅØ4„Å§„Åß„ÅÇ„ÇãÂøÖË¶Å„Åå„ÅÇ„Çä„Åæ„Åô");
      }

      const correctChoices = parsedResponse.choices.filter((choice) => choice.isCorrect);
      if (correctChoices.length !== 1) {
        throw new Error("Ê≠£Á≠î„ÅØ1„Å§„Åß„ÅÇ„ÇãÂøÖË¶Å„Åå„ÅÇ„Çä„Åæ„Åô");
      }

      return parsedResponse;
    } catch (error) {
      console.error("Quiz generation error:", error);
      throw new Error(`ÂïèÈ°å„ÅÆËá™ÂãïÁîüÊàê„Å´Â§±Êïó„Åó„Åæ„Åó„Åü: ${error}`);
    }
  }
}

export const quizGenerationService = new QuizGenerationService();
