import React, { useState } from 'react';
import Editor from '@monaco-editor/react';
import { useProblem } from '../contexts/ProblemContext';
import { quizGenerationService } from '../services/quizGenerationService';
import type { AutoGenerationResponse, QuizChoice } from '../types/quiz';

interface AutoGenerationModeProps {
  learningTopic: string;
  onQuizGenerated: (quiz: AutoGenerationResponse) => void;
  onGeneratingStateChange?: (isGenerating: boolean) => void;
}

export const AutoGenerationMode: React.FC<AutoGenerationModeProps> = ({ 
  learningTopic, 
  onQuizGenerated,
  onGeneratingStateChange
}) => {
  const { problemData } = useProblem();
  const [isGenerating, setIsGenerating] = useState(false);

  // ÁîüÊàê‰∏≠Áä∂ÊÖã„ÅÆÂ§âÊõ¥„ÇíË¶™„Å´ÈÄöÁü•
  const updateGeneratingState = (generating: boolean) => {
    setIsGenerating(generating);
    if (onGeneratingStateChange) {
      onGeneratingStateChange(generating);
    }
  };
  const [generatedQuiz, setGeneratedQuiz] = useState<AutoGenerationResponse | null>(null);
  const [error, setError] = useState<string>('');

  const handleGenerate = async () => {
    if (!learningTopic.trim()) {
      setError('Â≠¶ÁøíÈ†ÖÁõÆ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ');
      return;
    }

    if (!problemData.problem.trim() || !problemData.code.trim()) {
      setError('ÂïèÈ°åÊñá„Å®„Ç≥„Éº„Éâ„ÅåÂøÖË¶Å„Åß„Åô');
      return;
    }

    updateGeneratingState(true);
    setError('');

    try {
      const response = await quizGenerationService.generateQuiz({
        problemText: problemData.problem,
        code: problemData.code,
        language: problemData.language,
        learningTopic: learningTopic
      });

      // Ëß£Ë™¨„ÇíÁ©∫„Å´„Åó„Å¶Ê∏°„Åô
      const responseWithoutExplanation = { ...response, explanation: '' };
      setGeneratedQuiz(responseWithoutExplanation);
      onQuizGenerated(responseWithoutExplanation);
    } catch (error) {
      setError(error instanceof Error ? error.message : 'ÂïèÈ°å„ÅÆÁîüÊàê„Å´Â§±Êïó„Åó„Åæ„Åó„Åü');
    } finally {
      updateGeneratingState(false);
    }
  };

  const getChoiceStyle = (choice: QuizChoice) => ({
    padding: '10px',
    margin: '5px 0',
    border: choice.isCorrect ? '2px solid #4CAF50' : '1px solid #ddd',
    borderRadius: '4px',
    backgroundColor: choice.isCorrect ? '#f0f8f0' : 'white',
    fontSize: '14px'
  });

  return (
    <div>
      <div style={{ marginBottom: '20px' }}>
        <button
          onClick={handleGenerate}
          disabled={isGenerating}
          style={{
            padding: '12px 24px',
            backgroundColor: isGenerating ? '#ccc' : '#2196f3',
            color: 'white',
            border: 'none',
            borderRadius: '6px',
            cursor: isGenerating ? 'not-allowed' : 'pointer',
            fontSize: '14px',
            fontWeight: 'bold'
          }}
        >
          {isGenerating ? 'ÁîüÊàê‰∏≠...' : 'AI „ÅßÂïèÈ°å„ÇíËá™ÂãïÁîüÊàê'}
        </button>
      </div>

      {error && (
        <div style={{
          padding: '12px',
          backgroundColor: '#ffebee',
          border: '1px solid #f44336',
          borderRadius: '4px',
          color: '#d32f2f',
          marginBottom: '20px',
          fontSize: '14px'
        }}>
          {error}
        </div>
      )}

      {generatedQuiz && (
        <div>
          <h3 style={{ fontSize: '16px', fontWeight: 'bold', marginBottom: '16px' }}>
            ÁîüÊàê„Åï„Çå„ÅüÂïèÈ°å
          </h3>

          {/* ÂïèÈ°åÊñá */}
          <div style={{ marginBottom: '20px' }}>
            <h4 style={{ fontSize: '14px', fontWeight: 'bold', marginBottom: '8px' }}>
              ÂïèÈ°åÊñá
            </h4>
            <div style={{
              padding: '12px',
              backgroundColor: 'white',
              border: '1px solid #ddd',
              borderRadius: '4px',
              fontSize: '14px',
              lineHeight: '1.6',
              whiteSpace: 'pre-wrap'
            }}>
              {generatedQuiz.problemText}
            </div>
          </div>

          {/* Á©¥Âüã„ÇÅ„Ç≥„Éº„Éâ„ÄÅÈÅ∏ÊäûËÇ¢„ÄÅËß£Ë™¨ */}
          <div style={{ 
            display: 'flex',
            gap: '20px',
            marginBottom: '20px'
          }}>
            {/* Â∑¶ÂÅ¥: Á©¥Âüã„ÇÅ„Ç≥„Éº„Éâ */}
            <div style={{ flex: 1 }}>
              <h4 style={{ fontSize: '14px', fontWeight: 'bold', marginBottom: '8px' }}>
                Á©¥Âüã„ÇÅ„Ç≥„Éº„Éâ
              </h4>
              <div style={{ height: '600px', border: '1px solid #ddd', borderRadius: '4px' }}>
                <Editor
                  height="100%"
                  language={problemData.language}
                  value={generatedQuiz.codeWithBlanks}
                  onMount={(editor, monaco) => {
                    // ___BLANK___„Çí„Éè„Ç§„É©„Ç§„ÉàË°®Á§∫„Åô„Çã„Åü„ÇÅ„ÅÆ„Éá„Ç≥„É¨„Éº„Ç∑„Éß„É≥„ÇíËøΩÂä†
                    const model = editor.getModel();
                    if (model) {
                      const text = model.getValue();
                      const decorations = [];
                      const regex = /___BLANK___/g;
                      let match;
                      
                      while ((match = regex.exec(text)) !== null) {
                        const startPos = model.getPositionAt(match.index);
                        const endPos = model.getPositionAt(match.index + match[0].length);
                        
                        decorations.push({
                          range: new monaco.Range(
                            startPos.lineNumber,
                            startPos.column,
                            endPos.lineNumber,
                            endPos.column
                          ),
                          options: {
                            inlineClassName: 'my-blank-highlight'
                          }
                        });
                      }
                      
                      editor.deltaDecorations([], decorations);
                    }
                  }}
                  options={{
                    readOnly: true,
                    minimap: { enabled: false },
                    fontSize: 14,
                    lineNumbers: 'on',
                    wordWrap: 'on',
                    scrollBeyondLastLine: false,
                    automaticLayout: true,
                  }}
                  theme="vs-light"
                />
              </div>
            </div>

            {/* Âè≥ÂÅ¥: ÈÅ∏ÊäûËÇ¢„Å®Ëß£Ë™¨ */}
            <div style={{ 
              flex: 1,
              display: 'flex',
              flexDirection: 'column',
              height: '600px'
            }}>
              {/* Âè≥‰∏ä: ÈÅ∏ÊäûËÇ¢ */}
              <div style={{ flex: 1, marginBottom: '8px' }}>
                <h4 style={{ fontSize: '14px', fontWeight: 'bold', marginBottom: '8px' }}>
                  ÈÅ∏ÊäûËÇ¢
                </h4>
                <div style={{ 
                  // border: '1px solid #ddd', 
                  borderRadius: '4px',
                  padding: '8px',
                  backgroundColor: '#fafafa',
                  height: '200px',
                  overflow: 'auto'
                }}>
                  {generatedQuiz.choices.map((choice) => (
                    <div key={choice.id} style={getChoiceStyle(choice)}>
                      <strong>{choice.id}.</strong> {choice.text}
                      {choice.isCorrect && (
                        <span style={{ color: '#4CAF50', marginLeft: '8px' }}>‚úì Ê≠£Á≠î</span>
                      )}
                    </div>
                  ))}
                </div>
              </div>

              {/* Âè≥‰∏ã: Ëß£Ë™¨ÂÖ•Âäõ */}
              <div style={{ flex: 1 }}>
                <h4 style={{ fontSize: '14px', fontWeight: 'bold', marginBottom: '8px' }}>
                  Ëß£Ë™¨
                </h4>
                <textarea
                  placeholder="ÂïèÈ°å„ÅÆËß£Ë™¨„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ..."
                  style={{
                    width: '96%',
                    height: '280px',
                    padding: '12px',
                    border: '1px solid #ddd',
                    borderRadius: '4px',
                    fontSize: '14px',
                    lineHeight: '1.6',
                    resize: 'none',
                    fontFamily: 'inherit'
                  }}
                />
              </div>
            </div>
          </div>

        </div>
      )}
    </div>
  );
};